import React, { useState } from "react";
import Button from './Button';
import Input from './Input';

import button from './Button.module.css';
import itempicker from './ItemPicker.module.css';



function ItemPicker({ Array_id, itemArray, Item, setItem, btn }) {
    const [showModal, setShowModal] = useState(false);
    const [showModal_1, setShowModal_1] = useState(false);
    const [currentIndex, setCurrentIndex] = useState(0);

    const openModal = (event) => {
        event.preventDefault();
        setShowModal(true);
    };

    const closeModal = () => {
        setShowModal(false);
    }

    const handleItemClick = (event) => {
        event.preventDefault();
        if (Array_id !== 1 || event.target.textContent === "경기도") {
            setItem(event.target.textContent);
            closeModal();
        } else {
            setShowModal_1(true);
        }
    }

    const closeModal_1 = (event) => {
        event.preventDefault();
        setShowModal_1(false);
    }

    const dividedArrays = [];
    for (let i = 0; i < itemArray.length; i += 6) {
        dividedArrays.push(itemArray.slice(i, i + 6));
    }

    const handlePageClick = (index) => {
        setCurrentIndex(index);
    };

    const hasNextPage = currentIndex + 1 < dividedArrays.length;
    const hasPrevPage = currentIndex > 0;

    function doNothing(event) {
        event.preventDefault();
    };


    const Modal_1 = () => {
        return (
            <div className={itempicker.all}>
                <Button
                    className={button.img_buttonX}
                    onClick={closeModal}>
                </Button>
                <div className={itempicker.modal_title}>특별시/광역시/도</div>
                <hr className={itempicker.modal_hr}></hr>
                <div className={itempicker.box}>
                    <Input id='input' type='text' name='region1' required />
                    {dividedArrays[currentIndex].map((item) => (
                        <div
                            key={item}
                            className={itempicker.contents}
                            onClick={handleItemClick}
                            style={{ cursor: 'pointer', marginBottom: '10px' }}
                        >
                            {item}
                        </div>
                    ))}
                    <div className={itempicker.btn_page}>
                        {hasPrevPage && (
                            <button
                                key={currentIndex - 1}
                                className={itempicker.btn}
                                onClick={() => handlePageClick(currentIndex - 1)}
                                style={{ marginRight: '5px' }}
                            >
                                {currentIndex}
                            </button>
                        )}
                        <button
                            key={currentIndex}
                            className={itempicker.btn}
                            onClick={doNothing}
                            style={{
                                marginRight: '5px',
                                textDecoration: 'underline',
                                cursor: 'default'
                            }}
                        >
                            {currentIndex + 1}
                        </button>
                        {hasNextPage && (
                            <button
                                key={currentIndex + 1}
                                className={itempicker.btn}
                                onClick={() => handlePageClick(currentIndex + 1)}
                                style={{ marginRight: '5px' }}
                            >
                                {currentIndex + 2}
                            </button>
                        )}
                    </div>
                </div>
            </div>
        );
    };

    function Modal_2() {
        return (
            <div>
                <Button className={button.img_buttonX} onClick={closeModal}></Button>
                <div className={itempicker.modal_title}>시/군/구</div>
                <hr className={itempicker.modal_hr}></hr>
                <div className={itempicker.box}>
                    <Input id='input' type='text' name='region1' required />
                    {dividedArrays[currentIndex].map((item) => (
                        <div
                            key={item}
                            onClick={handleItemClick}
                            style={{ cursor: 'pointer', marginBottom: '10px' }}
                        >
                            {item}
                        </div>
                    ))}
                    <div>
                        {hasPrevPage && (
                            <button
                                key={currentIndex - 1}
                                className={itempicker.btn}
                                onClick={() => handlePageClick(currentIndex - 1)}
                                style={{ marginRight: '5px' }}
                            >
                                {currentIndex}
                            </button>
                        )}
                        <button
                            key={currentIndex}
                            className={itempicker.btn}
                            onClick={doNothing}
                            style={{
                                marginRight: '5px',
                                textDecoration: 'underline',
                                cursor: 'default'
                            }}
                        >
                            {currentIndex + 1}
                        </button>
                        {hasNextPage && (
                            <button
                                key={currentIndex + 1}
                                className={itempicker.btn}
                                onClick={() => handlePageClick(currentIndex + 1)}
                                style={{ marginRight: '5px' }}
                            >
                                {currentIndex + 2}
                            </button>
                        )}
                    </div>
                </div>
            </div>
        );
    }

    function Modal_3() {
        return (
            <div>
                <Button className={button.img_buttonX} onClick={closeModal}></Button>
                {itemArray.map((item) => (
                    <div
                        key={item}
                        onClick={handleItemClick}
                        style={{ cursor: "pointer", marginBottom: "10px" }}
                    >
                        {item}
                    </div>
                ))}
            </div>
        );
    }

    function Modal_4() {
        return (
            <div>
                <Button className={button.img_buttonX} onClick={closeModal}></Button>
                {itemArray.map((item) => (
                    <div
                        key={item}
                        onClick={handleItemClick}
                        style={{ cursor: "pointer", marginBottom: "10px" }}
                    >
                        {item}
                    </div>
                ))}
            </div>
        );
    }

    function Modal_5() {
        return (
            <div>
                <Button className={button.img_buttonX} onClick={closeModal}></Button>
                {itemArray.map((item) => (
                    <div
                        key={item}
                        onClick={handleItemClick}
                        style={{ cursor: "pointer", marginBottom: "10px" }}
                    >
                        {item}
                    </div>
                ))}
            </div>
        );
    }

    // 수정: className='Sign_up-button1'에서 className={itempicker.select}
    return (
        <div>
            <Button 
            onClick={openModal} 
            text="선택" 
            className={button.select} 
            disabled={btn}
            />

            {showModal && (
                <div className={itempicker.modal}>
                    {Array_id === 1 ? <Modal_1 /> : null}
                    {Array_id === 2 ? <Modal_2 /> : null}
                    {Array_id === 3 ? <Modal_3 /> : null}
                    {Array_id === 4 ? <Modal_4 /> : null}
                    {Array_id === 5 ? <Modal_5 /> : null}
                </div>


            )}

            {showModal_1 && (
                <div className={itempicker.modal_1_close}>
                    <Button className={button.img_buttonX} onClick={closeModal_1}></Button>
                    추후 서비스 예정
                </div>
            )}
        </div>
    );
};


export default ItemPicker;

//onClick={closeModal} => ()괄호를 붙이면 함수실행결과이고,
//이벤트핸들러 함수인 onClick에 함수변수를 넣고싶다면 말그대로 그냥 {closeModal}을 쓸 것
//아니면 {()=>closeModal()} 이렇게 써야함

//props대신 인자 쓸거면 중괄호 까먹지 말고

//조건부 부분은 중괄호로 감싸줘야함