
import Button from "./Button";

import { useState, useEffect, useRef } from "react";

import home from "../routes/Home.module.css";
import results from "./Results.module.css";

import IMG from "../img/locked.svg";


function Results_detail({ item, Item3, detailOpen, setDetailOpen }) {

    const [moreDetail, setMoreDetail] = useState(false);
    const onClick_moreDetail = (event) => {
        event.preventDefault();
        setMoreDetail(true);
    }
    const close_moreDetail = () => {
        setMoreDetail(false);
    }
    
    const closeDetail = () => {
        setDetailOpen(false);
    }
    
    return (
        <div>
            <div>파란바</div>
            <div>임대사업소:{Item3}</div>
            <div>
                <img src={IMG}></img>
                <div>
                    <p>농업용{item.type}</p>
                    <p>{item.content}</p>
                    <div>
                        <p>임대료(일 기준)</p>
                        <p>{item.price}</p>
                    </div>
                    <Button text="자세히 보기"
                        onClick={onClick_moreDetail}>
                    </Button>
                    {moreDetail &&
                        <div>
                            <div onClick={close_moreDetail}>X버튼</div>
                            <p>{item.type}</p>
                            <img src={IMG}></img>
                            <div>
                                {item.content}
                            </div>
                        </div>}
                </div>
            </div>
            <div className={home.calendar}>

            </div>

            <div>
                <p>{item.type}</p>
                <img src={IMG}></img>
                <div className={results.content2}>{item.content}</div>
            </div>

            <Button text="임대신청" className={home.requirement}></Button>
            <Button
                text="뒤로가기"
                className={home.requirement}
                onClick={closeDetail}
            ></Button>
        </div>
    )
}



function Results({ machineList, Item3 }) {

    // const [detail, setDetail] = useState(Array(machineList.length).fill(false));
    //배열 채우기
    const [detailOpen, setDetailOpen] = useState(false);
    const [detail_index, setDetail_index] = useState(null);



    console.log(machineList);
    const onClick_detail = (value) => (event) => {
        //div에서 클릭으로 value값 받기
        event.preventDefault();
        setDetail_index(value);
        console.log('detail_index', detail_index);
        setDetailOpen(true);
    }



    return (
        <div>
            <p>검색건수 | 총{machineList.length}건</p>
            <main>
                {!detailOpen
                    ? <ul>
                        {machineList.map((item, index) => {
                            return (
                                <li key={item.reservationId}>
                                    <div onClick={onClick_detail(index)}>
                                        <img src={IMG}
                                            alt="machine_img"
                                            className={home.result_img}></img>
                                        <p>{item.type}</p>
                                    </div>
                                </li>
                            )
                        })}
                    </ul>
                    : <Results_detail
                        item={machineList[detail_index]}
                        Item3={Item3}
                        detailOpen={detailOpen}
                        setDetailOpen={setDetailOpen}
                    >
                    </Results_detail>
                }
            </main>
        </div>
    )
}
export default Results;