import hmacSHA256 from "crypto-js/hmac-sha256";
import encBase64 from "crypto-js/enc-base64";
import { accessKey, secretKey } from "./NaverConfig";

const createAuthHeader = (httpMethod, url, dateHeader) => {
  const signature = hmacSHA256(`${httpMethod}\n\n\n${dateHeader}\n${url}`, secretKey);
  const signatureBase64 = encBase64.stringify(signature);

  return `NCP ${accessKey}:${signatureBase64}`;
};

export default createAuthHeader;